.converterContainer {
    margin-top: 10px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.converter {
    background-color: #f2f2f7;
    width: 640px;
    height: 100px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.converter i {
    font-size: 30px;
    cursor: pointer;
}

.converter input, select, i {
    margin: 0 5px;
}

input[type=number] {
    appearance: textfield;
}

.ban-overlay {
    width: 640px;
    height: 100px;
    position: absolute;
    background: rgba(0, 0, 0, .8);
    display: flex;
    justify-content: center;
    align-items: center;
}

.ban-overlay #ban-logo::before {
    opacity: 0;
    animation: in 1s forwards;
}

.ban-overlay:hover #ban-logo::before  {
    opacity: 1;
    animation: out .5s forwards;
}

@keyframes in {
    to {
        opacity: 1
    }
}

@keyframes out {
    50% {
        opacity: 0;
        font-size: 70px;
        font-weight: 400;
        line-height: 0px;
        content: "";
    }
    
    51% {
        opacity: 0;
        font-weight: 400;
        font-family: 'Roboto', sans-serif;
        line-height: 100px; 
        font-size: 20px;
    }
    
    100% {
        opacity: 1;
        font-weight: 400;
        font-family: 'Roboto', sans-serif;
        line-height: 100px; 
        font-size: 20px;
        content: "To use converter, please register on this site.";
    }
}

table {
    border-collapse: none;
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

th {
    padding: 10px;
    background-color: #f0fbffc2;
    font-weight: 200;
    border: 1px solid #253443;;
}

.TableHeader {
    font-weight: 700;
}

@media only screen and (max-width: 1000px)
{
    .Table{
        width: auto;
        display: flex;
        justify-content: center;
    }
}